body {
  font-family: 'Montserrat', sans-serif;
  position: relative;
}


@media (prefers-reduced-motion: no-preference) {
  .projects-spin {
    animation: App-logo-spin infinite 20s linear;
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}