@import '../../index.scss';

.skills {
    padding-top: 100px;

    &__inner {
        margin-left: 200px;
    }

    &__wrapper {
        display: flex;
        flex-wrap: wrap;
        max-width: 850px;
    }

    &__item-wrapper {
        display: flex;
        flex-direction: column;
        color: #fff;
    }

    &__item-wrapper {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        align-content: space-between;
        align-items: center;
        max-width: 170px;
        margin: 0 20px 40px 0;
        // margin-bottom: 40px;
        transition: all .3s ease-in-out;

        &:hover .skills__item {
            border: 1px solid $accentColor;
        }

        &:hover .skills__item-percent {
            color: $accentColor;
        }

        &:hover {
            transform: scale(1.1);
        }
    }

    &__item {
        border: 1px solid #999;
        width: 170px;
        padding: 50px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 100px;
        transition: all .3s ease-in-out;
    }

    &__item-img {
        max-width: 60px;
        max-height: 60px;
    }

    &__item-percent {
        margin-top: 30px;
        font-size: 30px;
        font-weight: 300;
        transition: all .3s ease-in-out;
    }

    &__item-name {
        margin-top: 20px;
    }
}

@media (max-width: 500px) {
    .skills {

        &__item-wrapper {
            margin: 0 10px 40px 0;
        }

        &__item {
            width: 120px;
            padding: 30px 0;
        }
    }
}